import axios from 'axios';


const instance = axios.create({
    //withCredentials: true
    baseURL: 'http://localhost:4000/',
    responseType: "json"
});


export const userPost = {
    postUser () {
        return instance.post(`users`, {
            fullName : "Percha",
            "photoUrl": "https://www.thesun.co.uk/wp-content/uploads/2019/10/NINTCHDBPICT000528842728.jpg"})
            .then(function (response) {
                console.log(response);
            })
            .catch(function (error) {
                console.log(error);
            });
    }
};



export const usersAPI = {
    getUsers()  {
        return instance.get(`users`)
        .then(response => {
            return response.data
        });
     },
     follow(userId) {
        return instance.post(`follow?page=${userId}`)
     },
     unfollow(userId) {
        return instance.delete(`follow?page=${userId}`)
    },
    getProfile(userId) {
        console.warn('Obsilete method. Please use profileAPI object');
        return profileAPI.getProfile(userId);
            
    }

}

export const profileAPI = {
    getProfile(userId) {
        return instance.get(`users` + userId);
    },
    getStatus(userId) {
        return instance.get(`profile` + userId);
    },
    updateStatus(status) {
        return instance.put(`profile/status`, { status: status });
    }
}

export const authAPI = {
    me() {
        return instance.get(`auth/me`);
    },
    login(email, password, rememberMe = false) {
        return instance.post(`auth/login`, { email, password, rememberMe });
    },
    logout() {
        return instance.delete(`auth/login`);
    }
}



